.profile__name {
    margin: 0;
    padding: 0;
    font-weight: 500;
    font-size: 42px;
    line-height: 114%;
    font-style: normal;
    max-width: 294px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}


@media screen and (max-width: 929px) {
    .profile__name {
        font-size: 35px;
        max-width: 290px;
    }
}

@media screen and (max-width: 644px) {
    .profile__name {
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        max-width: 189px;
    }
}